@import url('https://fonts.googleapis.com/css?family=Almarai&display=swap');
@import url('https://fonts.googleapis.com/css?family=Scheherazade&display=swap');

a {
  text-decoration: none;
  color: #eeee;
}
.number-circle {
  border-radius: 50%;
  width: 9rem;
  height: 9rem;
  padding: 8px;
  padding-top: 3rem;

  color: #f3ffff;
  text-align: center;

  font: 2rem Arial, sans-serif;
}

.confirmed {
  border: 4px solid #ff8149;
}

.deaths {
  border: 4px solid #ff0000;
}

.recovered {
  border: 4px solid #09e500;
}

.error {
  color: #ff0000;
  font-size: 2rem;
  padding-bottom: 3rem;
}

.global {
  text-align: center;
  padding-top: 9rem;
}

.global img {
  width: 13rem;
  height: 13rem;
  padding-right: 4rem;
}
.global .content {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  align-items: center;
}

.global .content .info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.global .content .info h2 {
  font-size: 2rem;
  font-weight: 100;
  margin-bottom: 1rem;
}

@media (max-width: 657px) {
  .global .content {
    flex-direction: column;
  }

  .global .info {
    margin-bottom: 3rem;
  }
  .global .content .info h2 {
    margin-bottom: 0.5rem;
  }
  .global img {
    padding-right: 0;
    margin-bottom: 1rem;
  }
}

footer {
  font-weight: 100;
  font-size: 1.3rem;
  position: absolute;
  left: 0;
  bottom: 0;
  text-align: center;
  padding: 1rem;
  background: #4444;
  height: 3.7rem;
  width: 100%;
  margin-top: 4rem;
}
