@import url(https://fonts.googleapis.com/css?family=Tajawal&display=swap);
@import url(https://fonts.googleapis.com/css?family=Almarai&display=swap);
@import url(https://fonts.googleapis.com/css?family=Scheherazade&display=swap);
:root {
  --bg-color: #1c1c1f;
  --primary-color: #e5e5e5;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-size: 62.5%;
  font-family: 'Tajawal', sans-serif;
  line-height: 1.7;
  background-color: #1c1c1f;
  background-color: var(--bg-color);
  color: #e5e5e5;
  color: var(--primary-color);
}

.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(201, 76, 76, 0.2);
  border-right: 1.1em solid rgba(201, 76, 76, 0.2);
  border-bottom: 1.1em solid rgba(201, 76, 76, 0.2);
  border-left: 1.1em solid #b30000;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.Map {
  height: 100vh;
  max-width: 100%;
  font-size: 0.9rem;
  font-weight: bold;
}

a {
  text-decoration: none;
  color: #eeee;
}
.number-circle {
  border-radius: 50%;
  width: 9rem;
  height: 9rem;
  padding: 8px;
  padding-top: 3rem;

  color: #f3ffff;
  text-align: center;

  font: 2rem Arial, sans-serif;
}

.confirmed {
  border: 4px solid #ff8149;
}

.deaths {
  border: 4px solid #ff0000;
}

.recovered {
  border: 4px solid #09e500;
}

.error {
  color: #ff0000;
  font-size: 2rem;
  padding-bottom: 3rem;
}

.global {
  text-align: center;
  padding-top: 9rem;
}

.global img {
  width: 13rem;
  height: 13rem;
  padding-right: 4rem;
}
.global .content {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-evenly;
  align-items: center;
}

.global .content .info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.global .content .info h2 {
  font-size: 2rem;
  font-weight: 100;
  margin-bottom: 1rem;
}

@media (max-width: 657px) {
  .global .content {
    flex-direction: column;
  }

  .global .info {
    margin-bottom: 3rem;
  }
  .global .content .info h2 {
    margin-bottom: 0.5rem;
  }
  .global img {
    padding-right: 0;
    margin-bottom: 1rem;
  }
}

footer {
  font-weight: 100;
  font-size: 1.3rem;
  position: absolute;
  left: 0;
  bottom: 0;
  text-align: center;
  padding: 1rem;
  background: #4444;
  height: 3.7rem;
  width: 100%;
  margin-top: 4rem;
}

.wrapper {
  text-align: center;
  position: absolute;
  top: 6rem;
  right: 2rem;
}

.select-container {
  background-color: #060606;
  color: #060606;
}

.flag-select__options {
  width: 15rem;
  top: 2rem;
  right: -2rem;
}

.flag-select__btn {
  color: #f3ffff;
}

#navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  background-color: inherit;
  z-index: 10;
}

#navbar.shadow {
  background-color: var(--bg-color);
}

.shadow {
  box-shadow: 0px 10px 5px 0px rgba(0, 0, 0, 0.75);
  -webkit-transition: all 0.3 ease-out;
  transition: all 0.3 ease-out;
}

#navbar ul li {
  padding: 1rem;
  padding-top: 1.5rem;
  font-size: 1.2rem;
  font-weight: bold;
}
#navbar ul li a {
  text-decoration: none;
  color: #f3ffff;
}
#navbar ul {
  list-style: none;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: flex-start;
}

#navbar ul li a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background-color: #b30000;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
}

#navbar ul li a.active::after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: #b30000;
}
#navbar ul li a:hover::after {
  width: 100%;
}

